package pl.reszetow.businesscard

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.*
import androidx.compose.material3.*
import androidx.compose.runtime.*
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.text.font.FontWeight
import kotlin.js.ExperimentalWasmJsInterop
import kotlin.js.definedExternally
import pl.reszetow.businesscard.components.BusinessCard
import pl.reszetow.businesscard.components.Footer
import pl.reszetow.businesscard.theme.AppThemeProvider
import pl.reszetow.businesscard.theme.LocalAppTheme

@Composable
fun App() {
    AppThemeProvider {
        val theme = LocalAppTheme.current

        BoxWithConstraints(
            modifier = Modifier
                .fillMaxSize()
                .background(theme.colors.Background)
        ) {
            val topPadding = maxHeight / 5
            Column(
                modifier = Modifier.fillMaxSize(),
                horizontalAlignment = Alignment.CenterHorizontally
            ) {
                Spacer(modifier = Modifier.height(topPadding))
                BusinessCard()
                Spacer(modifier = Modifier.weight(1f))
                Footer()
            }
        }
    }
}
